<style scoped>
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
  .date-wrap-inline {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  }
</style>

<template>
  <div>
    <el-dialog 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      :title="title" 
      :visible.sync="isShow" 
      width='720px'>
      <div class="search-form-wraper c-g-sfw">
        <el-form :model="searchContent" label-position="top" @submit.native.prevent>
          <el-form-item>
            <el-input class="search-input" v-model.trim="searchContent.searchValue" placeholder="供应商编号/关键字" size="small" @keyup.enter.native="doSearchHandel" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <el-table
          class="fixed-height"
          :data="tableData"
          row-key="platformCityGoodsId"
          empty-text="没有查询到您想要的数据"
          style="width:100%;"
          height="350"
          size="small">
          <el-table-column min-width="90px" prop="vendorNo" label="供应商编号"></el-table-column>
          <el-table-column min-width="120px" prop="vendorName" label="供应商名称"></el-table-column>
          <el-table-column min-width="70px" prop="price" label="市场价"></el-table-column>
          <el-table-column min-width="70px" prop="salePrice" label="售价"></el-table-column>
          <el-table-column min-width="80px" prop="vendorStock" label="活动库存"></el-table-column>
          <el-table-column min-width="210px" label="有效期">
            <template slot-scope="scope">
              <div class="date-wrap-inline" v-if="scope.row.validStartDate && scope.row.validEndDate">
                <div>
                  <p>{{scope.row.validStartDate.substr(0, 10)}}</p>
                  <p>{{scope.row.validStartDate.substr(11, 8)}}</p>
                </div>
                <div>&nbsp;-&nbsp;</div>
                <div>
                  <p>{{scope.row.validEndDate.substr(0, 10)}}</p>
                  <p>{{scope.row.validEndDate.substr(11, 8)}}</p>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            align="center"
            label="操作"
            width="100">
            <template slot-scope="scope" size="small">
              <el-button @click="chooseHandel(scope.row)" type="primary" size="mini">选择</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        isShow: false,
        title: '选择供应商', // 弹窗title
        searchContent: {
          searchValue: ''
        },
        tableData: [],
        tableData: [],
        platformCityGoodsId: ''
      }
    },
    methods: {
      setDefault(val) {
        this.title = val.title || '选择供应商'
        this.tableData = val.tableData && val.tableData.length ? JSON.parse(JSON.stringify(val.tableData)) : []
        this.tableDataTemp = val.tableData && val.tableData.length ? JSON.parse(JSON.stringify(val.tableData)) : []
        this.platformCityGoodsId = val.platformCityGoodsId || ''
        this.searchContent.searchValue = ''
        this.isShow = true
      },
      chooseHandel(row) {
        let params = {
          platformCityGoodsId: this.platformCityGoodsId,
          row
        }
        this.$emit("confirmChooseVendor", params)
        this.isShow = false
      },
      doSearchHandel() {
        let searchValueStr = this.searchContent.searchValue
        if(searchValueStr) {
          this.tableData = this.tableDataTemp.filter(item => {
            return item.vendorName.includes(searchValueStr) || item.vendorNo == searchValueStr
          })
        }else {
          this.tableData = [...this.tableDataTemp]
        }
      }
    }
  }
</script>


