<style scoped>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .c-g-sfw {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
  }
</style>

<template>
  <div>
    <el-dialog 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      :title="title" 
      :visible.sync="isShow" 
      width='720px'>
      <div class="search-form-wraper c-g-sfw">
        <el-form :model="searchContent" label-position="top" @submit.native.prevent>
          <el-form-item>
            <el-cascader
              placeholder="请选择分类"
              class="search-input"
              :options="categoryList"
              change-on-select
              size="small"
              clearable
              filterable
              @change="doCategorysChangeHandel"
              :props="cateDefaultProps"
              v-model="searchContent.categoryIdList">
            </el-cascader>
          </el-form-item>
          <el-form-item>
            <el-input class="search-input" v-model.trim="searchContent.searchValue" placeholder="SKU编码/商品名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <div>
        <el-table
          class="fixed-height"
          ref="multipleTable"
          v-loading="loading"
          :data="tableData"
          row-key="platformCityGoodsId"
          empty-text="没有查询到您想要的数据"
          style="width:100%;"
          height="350"
          @select="handleSelect"
          @select-all="handleSelectAll"
          size="small">
          <el-table-column
            type="selection"
            align="center"
            :selectable = "selectableHandel">
          </el-table-column>
          <el-table-column min-width="226px" label="商品信息">
            <template slot-scope="scope">
              <div class="ll-p-m-w">
                <div class="ll-img-w">
                  <img v-if="scope.row.platformImageUrl && scope.row.platformImageUrl != ''" :src="scope.row.platformImageUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                  <img v-else src="/shop/static/img_error.png" alt="">
                </div>
                <div class="ll-p-m-c-r">
                  <div>{{scope.row.platformCityGoodsName || '--'}}</div>
                  <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                  <div class="ll-erp-p-no">商品ID：{{scope.row.platformCityGoodsId || '--'}}</div>
                </div>
              </div>
            </template> 
          </el-table-column>
          <el-table-column min-width="120px" prop="spec" label="规格"></el-table-column>
          <el-table-column min-width="120px" prop="categoryName" label="分类"></el-table-column>
        </el-table>
        <!-- 分页条 -->
        <el-pagination
          v-show="tableData.length>0"
          class="tc mt20"
          background
          @size-change="changeSize"
          @current-change="changeCurrent"
          :current-page.sync="pageNum"
          :page-sizes="pageSizes"
          layout="total, sizes, prev, pager, next, jumper"
          :pager-count="5"
          :total="total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="confirmClose" size="mini" :disabled="isDisabled || checkedList.length === 0">导 入</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import config from "@/utils/config"
  const requestUrl = {
    // 获取三方平台店内分类
    getCategoryTree: '/plateform/a/category/getCategoryTree',
    // 选择区域商品
    selectCityGoods: '/plateform/a/baseCommon/city/selectPreSaleCityGoodsList'
  }
  export default {
    data() {
      return {
        pageSizes: config.pageSizes10,
        isDisabled: false,
        isShow: false,
        title: '选择商品', // 弹窗title
        loading: false,
        pageNum: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          regionCode: '',
          saleType: 4,
          vendorType: 2,
          categoryIdList: [],
          searchValue: '', // sku编码，商品名称搜索
          categoryId: '' // 商品分类ID
        },
        tableData: [],
        // 选中商品
        checkedList: [],
        // 默认分类参数
        cateDefaultProps: {
          children: 'categoryVoList',
          label: 'categoryName',
          value: 'categoryId'
        },
        isClickSublimitCloseDialog: true, // 点击导入关闭弹窗  默认直接关闭
        categoryList: [] // 商品分类id

      }
    },
    methods: {
      /**
       * [setDefault 设置基本选中]
       * @param {[type]} _val [description]
       */
      setDefault(val) {
        this.title = val.title || ''
        this.searchContent.regionCode = val.regionCode || ''
        this.searchContent.vendorType = val.vendorType || 2
        this.checkedList = val.checkedList && val.checkedList.length ? JSON.parse(JSON.stringify(val.checkedList)) : []
        this.isClickSublimitCloseDialog = val.isClickSublimitCloseDialog === true || val.isClickSublimitCloseDialog === false ? val.isClickSublimitCloseDialog : true
        this.isShow = true
        // 获取三方平台分类
        this.getCategoryId()
        this.doSearchHandel(true)
      },
      /**
       * [getCategoryId 三方平台店内分类]
       * @return {[type]} [description]
       */
      getCategoryId() {
        this.$http.post(requestUrl.getCategoryTree, {DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              for(let item of res.data) {
                if(item.categoryVoList && item.categoryVoList.length === 0) {
                  item.categoryVoList = null
                }
              }
              this.categoryList = res.data
            }else {
              this.categoryList = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(三方平台店内分类):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [doSearchHandel 查询列表接口]
       * @param  {[type]} _param [description]
       * @return {[type]}         [description]
       */
      doSearchHandel(_param) {
        let spliceLen = this.searchContent.categoryIdList.length - 1
        this.searchContent.categoryId = this.searchContent.categoryIdList[spliceLen]
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(requestUrl.selectCityGoods, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize}).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data && res.data.result && res.data.result.length) {
              this.tableData = res.data.result
              this.total = res.data.total
            }else {
              this.tableData = []
            }
            this.defaultSelectFun()
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      handleSelect(checkedRows, row) {
        if(checkedRows.includes(row)) {
          this.checkedList.push(row)
        }else {
          const index = this.checkedList.findIndex(item => {
            return item.storeNo === row.storeNo
          })
          this.checkedList.splice(index, 1)
        }
      },
      handleSelectAll(arr) {
        if(arr.length) {
          // 全选
          this.tableData.forEach(i => {
            const index = this.checkedList.findIndex(j => {
              return j.platformCityGoodsId === i.platformCityGoodsId
            })
            if(index === -1) {
              this.checkedList.push(i)
            }
          })
        }else {
          // 全不选
          this.tableData.forEach(i => {
            const index = this.checkedList.findIndex(j => {
              return j.platformCityGoodsId === i.platformCityGoodsId
            })
            if(index !== -1) {
              this.checkedList.splice(index, 1)
            }
          })
        }
      },
      defaultSelectFun() {
        let rows = []
        this.tableData.forEach(i => {
          this.checkedList.forEach(j => {
            if(i.platformCityGoodsId == j.platformCityGoodsId) {
              i.disabled = j.disabled
              rows.push(i)
            }
          })
        })
        if (rows.length > 0) {
          this.$refs.multipleTable.clearSelection()
          setTimeout(() => {
            rows.forEach(item => {
              this.$refs.multipleTable.toggleRowSelection(item)
            });
          }, 100)
        } else {
          setTimeout(() => {
            this.$refs.multipleTable.clearSelection()
          }, 100)
        }
      },
      confirmClose() {
        this.isDisabled = true
        let params = {
          checkedList: this.checkedList
        }
        this.$emit("confirmChooseGoods", params)
        if(this.isClickSublimitCloseDialog) {
          this.isShow = false
          this.isDisabled = false
        }
      },
      doCategorysChangeHandel() {
        this.doSearchHandel(true)
      },
      selectableHandel(row, index) {
        return !row.disabled
      },
      closeDialog() {
        this.isShow = false
      },
      closeDiasbled() {
        this.isDisabled = false
      }
    }
  }
</script>


